@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Silkscreen&display=swap');

*{
  box-sizing:border-box;
  margin:0;
  padding:0;
  font-family:"Outfit", sans-serif;
}
body{
  background: #000;

}
h1,h4,p,
a{
  color: #fff;
  text-decoration:none;
}
ul{
  list-style-type: none;
}
.btn {
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background:rgb(256,217,16);
  color: #222;
  border: 1px solid #fff;
  font-weight:600;
  cursor:pointer;
}

.btn-light{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: transparent;
  color: #fff;
  border: 1px solid #fff;
  font-weight:600;
  cursor:pointer;
}
.btn.hover {
  background:rgba(255,255,255,0.2);
  color:#fff;
  transition: 0.3s;
}
