.hero-img{
    width:100%;
    background: rgba(0,0,0,0.5);
    height:60vh;
    position: relative;
}

.hero-img::before{
    content: "";
    background: url("https://images.unsplash.com/photo-1475257026007-0753d5429e10?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTA2fHxwcm9ncmFtbWluZ3xlbnwwfHwwfHw%3D&auto=format&fit=crop&w=600&q=60");
    background-color: cover;
    height:100%;
    width:100%;
    position: absolute;
    top:0;
    left:0;
    z-index: -1;
}
.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}
.heading{
    width:100%;
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
}


.hero-img h1{
    font-size:1.4rem;
}


@media screen and (max-width:640px) {
    .hero-img h1{
        font-size:2rem;
    }

}